<template>
    <div></div>
</template>

<script setup>
import { ref, onMounted, defineProps } from 'vue';
import { GeoJSON } from 'leaflet';

const props = defineProps(['map']);
const geoJsonData = ref(null);

onMounted(async () => {
  const response = await fetch('/geojson/ZIE_2021.geojson');
  geoJsonData.value = await response.json();

  // Ajouter la couche GeoJSON à la carte
  if (geoJsonData.value) {
    const geoJsonLayer = new GeoJSON(geoJsonData.value, {
      style: () => ({
        color: '#db1e2a',
        weight: getWeight(props.map.getZoom()),  // Initialiser avec le poids en fonction du niveau de zoom initial
        opacity: 1,
        fillOpacity: 0,
      }),
    });

    // Ajouter la couche à la carte
    geoJsonLayer.addTo(props.map);

    // Mise à jour de l'épaisseur du trait selon le niveau de zoom
    props.map.on('zoomend', () => {
      geoJsonLayer.setStyle({
        weight: getWeight(props.map.getZoom()), 
      });
    });
  }
});

// Fonction pour déterminer le poids du trait en fonction du niveau de zoom
function getWeight(zoomLevel) {
  return zoomLevel >= 12 ? 5 : 2;  // Si le zoom est >= 12, le poids est 5, sinon 2
}
</script>
