<template>
    <div v-if="laisseDeMerData && chartOptions && series && series.length > 0">
        <apexchart 
            :key="ldmDataKey" 
            type="bar" 
            height="300" 
            :options="chartOptions" 
            :series="series" 
        />
    </div>
  </template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  laisseDeMerData: {
    type: Object,
    required: true,
  },
});

const chartOptions = ref({});
const series = ref([]);

// Mapper les propriétés des données pour afficher les labels correspondants
const categoriesMap = {
  P_sol_nu: "Sol nu",
  P_plantes: "Plantes",
  P_deb_veg: "Végétaux",
  P_deb_alg: "Algues",
  P_deb_anim: "Animaux",
  P_deb_min: "Minéraux",
  P_deb_anth: "Anthropique",
  P_plastiqu: "Plastique",
  P_caoutcho: "Caoutchouc",
  P_textile: "Textile",
  P_papier: "Papier",
  P_bois: "Bois",
  P_metal: "Métal",
  P_verre: "Verre",
  P_dejectio: "Déjections",
  P_polluant: "Polluant",
  P_organiqu: "Organique",
  P_dech_aut: "Autres",
};

// Préparer les catégories et les valeurs pour le graphique
watch(() => props.laisseDeMerData, (data) => {
  console.log('Données mises à jour:', data);
  if (!data) {
    console.log('Données laissées de mer effacées');
  }
  if (data) {
    const categories = Object.keys(categoriesMap);
    const seriesData = categories.map((key) => data[key] || 0);

    chartOptions.value = {
      chart: {
        type: 'bar',
        height: 300,
      },
      xaxis: {
        categories: categories.map((key) => categoriesMap[key]),
        labels: {
          rotate: -90, 
          style: {
            fontSize: '0.8em',
            fontWeight: 'bold',
          },
        },
      },
      yaxis: {
        title: {
          text: 'Nombre',
        },
      },
      tooltip: {
        y: {
          formatter: (val) => `${val}`,
        },
      },
    };

    // Mettre à jour les séries de données
    series.value = [
      {
        name: 'Nombre',
        data: seriesData,
      },
    ];
  }
}, { immediate: true });
</script>