<template>
  <div id="cadre_info" v-if="CadreInfoVisible" :class="{ show: isVisible }">
    <button class="ferme-btn" @click="fermeCadreInfo">x</button>
    <h1>{{ nomCommune }}</h1>
    <div class="legende">
      <!-- Partie texte (60%) -->
      <div class="legende-texte">
        <span class="trait_rouge">
          La Zone d’Intervention Exceptionnelle : ZIE
        </span>
        <br />
        <span class="trait_orange">
          La Zone d’Intervention Sélective : ZIS
        </span>
        <br />
        <span class="trait_vert">
          La Zone d’Intervention Globale : ZIG
        </span>
        <br />
        <span class="trait_pointille">
          Zone d’Intérêt Écologique
        </span>
        <span class="trait_ldm">
          Laisse de mer
        </span>
        <span class="carre_pnm">
          Parc Naturel Marin EPMO
        </span>
      </div>
      <!-- Partie cercles (40%) -->
      <div class="legende-cercles">
        <a :href="`../public/Fiches_communes/${nomCommune}.pdf#view=FitH`" target="_blank" class="lien">
          Téléchargez<br>la fiche Commune
        </a>
        <div class="cercle-container">
          <div class="cercle grand">
            <div class="cercle moyen">
              <div class="cercle petit"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="pointInfoDechets">
      <h3>Macro-déchets :</h3>
      <p><strong>Site :</strong> {{ pointInfoDechets.Site }}</p>
      <p><strong>Nombre :</strong> {{ pointInfoDechets.Nombre_TOT }}</p>
      <p><strong>Poids :</strong> {{ pointInfoDechets.Poids_TOT }} kg</p>
    </div>
    <ChartsMacroDechetsPie v-if="osparData && CadreInfoVisible" :osparData="osparData" :key="osparDataKey" />
    <component :is="contenuCommune" v-if="contenuCommune"></component>
    <div v-if="traitInfo">
      <h3>Laisse de mer :</h3>
      <p><strong>Site :</strong> {{ traitInfo.Site }}</p>
      <p><strong>Substrat :</strong> {{ traitInfo.Substrat }}</p>
      <p><strong>Largeur :</strong> {{ traitInfo.Largeur }} cm</p>
      <p><strong>Épaisseur :</strong> {{ traitInfo.Epaisseur }} cm</p>
    </div>
    <ChartsLaisseDeMerColumn :laisseDeMerData="ldmData" />
  </div>
</template>

<script setup>
import { ref, onMounted, defineProps, nextTick } from 'vue';
import L, { GeoJSON } from 'leaflet';
import ChartsMacroDechetsPie from './ChartsMacroDechetsPie.vue';
import ChartsLaisseDeMerColumn from './ChartsLaisseDeMerColumn.vue';

const emit = defineEmits(['CadreIntroInvisible']);
const props = defineProps(['map']);

const geoJsonData = ref(null);
const geoJsonPointsDechets = ref(null);
const CadreInfoVisible = ref(false);
const isVisible = ref(false);
let geoJsonLayer;
let geoJsonPointsDechetsLayer;
let selectedLayer = null;
const traitInfo = ref(null); // Laisse de mer
const geoJsonTraits = ref(null); // Laisse de mer
const nomCommune = ref('');
const contenuCommune = ref(null);
const pointInfoDechets = ref(null);
const osparData = ref(null);
const osparDataAll = ref([]); // Toutes les données OSPAR
const osparDataKey = ref(0);
const ldmData = ref(null);
const ldmDataKey = ref(0);
const activeCommune = ref(null); // Commune actuellement sélectionnée

const fermeCadreInfo = () => {
  isVisible.value = false;
  setTimeout(() => {
    CadreInfoVisible.value = false;
    if (selectedLayer) {
      geoJsonLayer.resetStyle(selectedLayer);
      selectedLayer = null;
    }
  }, 700);
};

const showCadreInfo = () => {
  CadreInfoVisible.value = true;
  setTimeout(() => (isVisible.value = true), 50);
};

onMounted(async () => {
  const response = await fetch('/geojson/PNM_EPMO_Communes.geojson');
  geoJsonData.value = await response.json();

  const pointsResponse = await fetch('/geojson/Nombre_Poids_Macrodechets.geojson');
  geoJsonPointsDechets.value = await pointsResponse.json();

  const osparResponse = await fetch('/geojson/OSPARMacroDechets.geojson');
  osparDataAll.value = await osparResponse.json(); // Récupérer toutes les données OSPAR

  const traitResponse = await fetch('/geojson/Typologie_laisse_de_mer.geojson'); // Chargez le GeoJSON des traits (laisse de mer)
  geoJsonTraits.value = await traitResponse.json();




  const onEachFeature = (feature, layer) => {
  layer.on({
    mouseover: () => handleMouseOver(layer, feature),
    mouseout: (e) => handleMouseOut(e, layer),
    click: () => handleCommuneClick(feature, layer),
  });
};

// Gestion du survol
const handleMouseOver = (layer, feature) => {
  if (props.map.getZoom() <= 11 && layer !== selectedLayer) {
    layer.setStyle({
      color: '#564949',
      weight: 1,
      opacity: 1,
      fillColor: '#EAC198',
      fillOpacity: 0.3,
    });

    // Positionnement et ouverture du pop-up lors du survol
    const popupPosition = layer.getBounds().getCenter();
    const popup = L.popup({
      closeButton: false,
      className: 'custom-popup',
      offset: L.point(-110, 45),
    })
      .setLatLng(popupPosition)
      .setContent(feature.properties.NOM)  // Contenu du pop-up : nom de la commune

    // Ouvrir le pop-up uniquement si le zoom est inférieur ou égal à 11
    if (props.map.getZoom() <= 12) {
      popup.openOn(props.map);
    } else {
      props.map.closePopup(); // Fermer le pop-up si zoom > 11
    }
  }
};

// Gestion du quitter le survol
const handleMouseOut = (e, layer) => {
  if (layer !== selectedLayer) {
    geoJsonLayer.resetStyle(e.target);  // Réinitialiser le style
  }
  props.map.closePopup();  // Fermer le pop-up
};

// Gestion du clic sur la commune
const handleCommuneClick = async (feature, layer) => {
  pointInfoDechets.value = null;
  osparData.value = null;
  traitInfo.value = null;
  ldmData.value = null;

  // Zoom sur la commune et déplacement de la carte
  const zoomLevel = 13;
  const center = layer.getBounds().getCenter();
  const offsetPosition = L.latLng(center.lat, center.lng + 0.03);

  props.map.flyTo(offsetPosition, zoomLevel, { duration: 0.7 });

  if (selectedLayer) {
    geoJsonLayer.resetStyle(selectedLayer);  // Réinitialiser le style du précédent sélectionné
  }
  
  // Appliquer un nouveau style pour la commune sélectionnée
  layer.setStyle({
    color: '#564949',
    weight: 3,
    fillOpacity: 0.1,
    fillColor: '#16A8C1'
  });

  selectedLayer = layer;

  // Mettre à jour les données de la commune active
  nomCommune.value = feature.properties.NOM;
  activeCommune.value = feature.properties.NOM;  // Mettre à jour la commune active

  emit('CadreIntroInvisible');
  showCadreInfo();
};



  
  //MACRO-DECHETS
  const filterPointsDechetsParCommune = () => {
    if (!activeCommune.value) return;

    // Supprimer la couche de points de déchets existante, s'il y en a une
    if (geoJsonPointsDechetsLayer) {
      props.map.removeLayer(geoJsonPointsDechetsLayer);
    }

    // Filtrer les points en fonction de la commune active
    const filteredPoints = geoJsonPointsDechets.value.features.filter(
      (feature) => feature.properties.Commune === activeCommune.value
    );

    // Créer une nouvelle couche GeoJSON pour les points filtrés
    geoJsonPointsDechetsLayer = new GeoJSON(
      { type: 'FeatureCollection', features: filteredPoints },
      {
        pointToLayer: (feature, latlng) => {
          const marker = createPointMarker(feature, latlng);
          marker.on('click', () => handlePointClick(feature)); // Ajouter l'événement click sur chaque point
          return marker;
        },
      }
    );

    // Ajouter la couche des points à la carte
    geoJsonPointsDechetsLayer.addTo(props.map);
  };

  const createPointMarker = (feature, latlng) => {
    const radius = feature.properties.Poids_TOT > 100 ? 25 : feature.properties.Poids_TOT > 1 ? 10 : 4;
    return L.circleMarker(latlng, {
      radius: radius,
      fillColor: 'green',
      color: '#000',
      weight: 1,
      opacity: 1,
      fillOpacity: 0.4,
    });
  };

  const handlePointClick = (feature) => {
    traitInfo.value = null;
    ldmData.value = null;
    ldmDataKey.value++;
    // Affichage des informations sur le point
    pointInfoDechets.value = {
      Site: feature.properties.Site,
      Nombre_TOT: feature.properties.Nombre_TOT.toLocaleString('fr-FR'),
      Poids_TOT: feature.properties.Poids_TOT,
    };
    // Sélection du site de déchets
    selectSiteDechets(feature.properties.ID);
  };

  // Afficher les points après la fin du zoom
  props.map.on('zoomend', () => {
    if (props.map.getZoom() >= 12 && activeCommune.value) {
      filterPointsDechetsParCommune(); // Afficher les points pour la commune active
    } else if (geoJsonPointsDechetsLayer) {
      props.map.removeLayer(geoJsonPointsDechetsLayer); // Supprimer les points si zoom < 12
    }
  });

  if (geoJsonData.value) {
    geoJsonLayer = new GeoJSON(geoJsonData.value, {
      style: { color: '#564949', weight: 1, opacity: 1, fillColor: '#EAC198', fillOpacity: 0.5 },
      onEachFeature: onEachFeature,
    });

    geoJsonLayer.addTo(props.map);
  }

  
  //GRAPHIQUE MACRO-DECHETS
  const selectSiteDechets = async (siteID) => {
    const siteData = osparDataAll.value.features.find((f) => f.properties.ID === siteID);

    osparData.value = siteData ? { ...siteData.properties } : null;

    osparDataKey.value++;
    await nextTick();

    if (siteData) {
      osparData.value = {
        OSPAR_1: siteData.properties.OSPAR_1 || 0,
        OSPAR_2: siteData.properties.OSPAR_2 || 0,
        OSPAR_3: siteData.properties.OSPAR_3 || 0,
        OSPAR_4: siteData.properties.OSPAR_4 || 0,
        OSPAR_5: siteData.properties.OSPAR_5 || 0,
        OSPAR_6: siteData.properties.OSPAR_6 || 0,
        OSPAR_7: siteData.properties.OSPAR_7 || 0,
        OSPAR_8: siteData.properties.OSPAR_8 || 0,
        OSPAR_9: siteData.properties.OSPAR_9 || 0,
        OSPAR_10: siteData.properties.OSPAR_10 || 0,
        OSPAR_11: siteData.properties.OSPAR_11 || 0,
        OSPAR_12: siteData.properties.OSPAR_12 || 0,
      };
    } else {
      osparData.value = null;
    }
  };



 // LAISSE DE MER
const onEachFeatureTrait = (feature, layer) => {
  layer.on({
    click: () => handleTraitClick(feature), // Ajouter l'événement click sur chaque trait
  });
};

// Calcul de l'épaisseur en fonction du niveau de zoom
const calculateWeight = (zoomLevel) => {
  return zoomLevel >= 13 ? 8 : 0;
};

// Couche GeoJSON pour les traits (laisse de mer)
const geoJsonTraitsLayer = new GeoJSON(geoJsonTraits.value, {  
  onEachFeature: onEachFeatureTrait,
  style: () => {
    const zoomLevel = props.map.getZoom();
    const weight = calculateWeight(zoomLevel); 

    return {
      color: '#9A7B67',
      weight: weight,  // Poids ajusté en fonction du zoom
      opacity: 1,
      fillOpacity: 0,
    };
  },
});

// Ajoute la couche à la carte
geoJsonTraitsLayer.addTo(props.map);

// Mise à jour du style de la couche selon le niveau de zoom
const updateGeoJsonTraitsLayerStyle = () => {
  const zoomLevel = props.map.getZoom();
  const weight = calculateWeight(zoomLevel); // Utilise la même fonction pour mettre à jour le poids

  geoJsonTraitsLayer.setStyle({
    weight: weight, // Met à jour le poids de la couche
  });
};

// Appliquer le style dès que la couche est ajoutée (en cas de zoom initial)
updateGeoJsonTraitsLayerStyle();

// Écoute le changement du zoom et met à jour le style
props.map.on('zoom', updateGeoJsonTraitsLayerStyle);

const handleTraitClick = (feature) => {
  pointInfoDechets.value = null;
  osparData.value = null;
  traitInfo.value = feature.properties || null;
  console.log("Informations sur le trait sélectionné :", traitInfo.value);
  selectSiteLdm(feature.properties?.ID);
};



// GRAPHIQUE LAISSE DE MER
const selectSiteLdm = async (siteID) => {
  const siteDataLdm = geoJsonTraits.value.features.find(
    (f) => f.properties?.ID === siteID
  );

  if (siteDataLdm) {
    const { properties } = siteDataLdm;
    ldmData.value = {
      ID: properties?.ID,
      Site: properties?.Site || 'Site Inconnu',
      P_sol_nu: properties?.P_sol_nu || 0,
      P_plantes: properties?.P_plantes || 0,
      P_deb_veg: properties?.P_deb_veg || 0,
      P_deb_alg: properties?.P_deb_alg || 0,
      P_deb_anim: properties?.P_deb_anim || 0,
      P_deb_min: properties?.P_deb_min || 0,
      P_deb_anth: properties?.P_deb_anth || 0,
      P_plastiqu: properties?.P_plastiqu || 0,
      P_caoutcho: properties?.P_caoutcho || 0,
      P_textile: properties?.P_textile || 0,
      P_papier: properties?.P_papier || 0,
      P_bois: properties?.P_bois || 0,
      P_metal: properties?.P_metal || 0,
      P_verre: properties?.P_verre || 0,
      P_dejectio: properties?.P_dejectio || 0,
      P_polluant: properties?.P_polluant || 0,
      P_organiqu: properties?.P_organiqu || 0,
      P_dech_aut: properties?.P_dech_aut || 0,
    };
  }


  ldmDataKey.value++;
  await nextTick();
};

  
});
</script>