<template>
    <div>
      <ApexCharts :options="chartOptions" :series="series" type="pie" width="525" />
    </div>
  </template>
  
  <script setup>
  import { ref, watch } from 'vue';
  import ApexCharts from 'vue3-apexcharts'; // Import d'ApexCharts
  
  // Définir la prop pour recevoir les données des macro-déchets
  const props = defineProps({
    osparData: Object
  });
  
  // Initialisation des séries du graphique
  const series = ref([]);
  
  // Définir les options du graphique
  const chartOptions = ref({
    chart: {
      type: 'pie',
      width: 380,
    },
    labels: [
      'Plastique et polystyrène', 
      'Caoutchouc', 
      'Textile', 
      'Papier et carton', 
      'Bois', 
      'Métal', 
      'Verre', 
      'Poterie et céramique', 
      'Déchets sanitaires', 
      'Déchets médicaux', 
      'Déjections animales', 
      'Autres polluants'
    ],
    responsive: [
      {
        breakpoint: 480,
        options: {
          chart: {
            width: 200
          },
          legend: {
            position: 'bottom'
          }
        }
      }
    ]
  });
  
  // Fonction pour mettre à jour les séries avec les nouvelles données
  const updateChart = (data) => {
    if (data) {
      series.value = [
        data.OSPAR_1,
        data.OSPAR_2,
        data.OSPAR_3,
        data.OSPAR_4,
        data.OSPAR_5,
        data.OSPAR_6,
        data.OSPAR_7,
        data.OSPAR_8,
        data.OSPAR_9,
        data.OSPAR_10,
        data.OSPAR_11,
        data.OSPAR_12
      ];
    }
  };
  
  // Observer les changements dans les données des macro-déchets et mettre à jour le graphique
  watch(() => props.osparData, (newData) => {
    updateChart(newData); // Met à jour le graphique avec les nouvelles données
  });
  </script>